<template>
  <el-row v-loading.fullscreen.lock="loadingPage">
    <el-col :span="24">
      <el-row class="text-align-right">
        <div class="domain-search-input">
          <el-input placeholder="请输入规则名进行搜索" prefix-icon="el-icon-search" v-model="ruleSearch"></el-input>
        </div>
        <a class="el-button el-button--primary"
          :href="'/#/' + backUrl +'-edit/' + ruleGroupUuid + '/group_rule/new'">添加规则</a>
        <a class="el-button el-button--primary is-plain" :href="'/#/' + backUrl +'/group_rule'">返回</a>
      </el-row>
      <div class="demo-block">
        <el-table :data="
          tableData.filter(
            (data) =>
              !ruleSearch ||
              data.domain.toLowerCase().includes(ruleSearch.toLowerCase())
          )
        " style="width: 100%">
          <el-table-column prop="rule_name" label="规则名称"></el-table-column>
          <el-table-column prop="rule_detail" label="描述"></el-table-column>
          <el-table-column prop="rule_num" label="关联网站"></el-table-column>
          <el-table-column prop="update_time" label="更新时间"></el-table-column>

          <el-table-column label="操作" align="right">
            <template #default="scope">
              <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text">编辑
              </el-button>


              <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                <p>确定删除吗？</p>
                <div style="text-align: right; margin: 0">
                  <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                  <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                  </el-button>
                </div>
                <template #reference>
                  <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                </template>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      ruleSearch: "",
      loadingPage: false,
      loading: false,
      tableData: [],
      ruleGroupUuid: "",
      backUrl:"web-rule-manage",
    };
  },

  mounted() {
    const route = useRoute()
    this.ruleGroupUuid = route.params.ruleGroupUuid;
    this.backUrl = route.params.backUrl;
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      var postUrl = "/waf/waf_get_sys_web_rule_protection_list";
      if (this.backUrl == 'sys-web-white-rule') { 
        postUrl = "/waf/waf_get_sys_web_white_rule_list";
      }
      if (this.backUrl == 'sys-flow-white-rule') { 
        postUrl = "/waf/waf_get_sys_flow_white_rule_list";
      }
      if (this.backUrl == 'sys-flow-rule-protection') { 
        postUrl = "/waf/waf_get_sys_flow_rule_protection_list";
      }
      JXAjax(
        "post",
        postUrl,
        { rule_type: "group_rule", rule_group_uuid: t.ruleGroupUuid },
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    handleEdit(data) { 
      window.location.href = '/#/' + this.backUrl + '-edit/' + this.ruleGroupUuid + '/group_rule/' + data.rule_uuid;
    },

    handleDelete(data) {
      var t = this;
      t.loading = true;
      JXAjax(
        "post",
        "/waf/waf_del_sys_web_rule_protection",
        { rule_uuid: data.rule_uuid },
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}
.icon-success {
  color: #67c23a;
  margin-right: 5px;
}
.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}
.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}
.domain-setting {
  float: right;
}
.domain-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}
.domain-search-input {
  display: block;
  float: left;
  text-align: left;
}
.domain-search-input .el-input {
  width: 100%;
}
.no-padding {
  padding: 0;
}
.node-detail span {
  font-size: 12px;
}
.domain-tabs {
  margin-bottom: 18px;
}
</style>
